import { LitElement, html, css, PropertyValues } from 'lit';
import { customElement, property, state } from 'lit/decorators.js';
import { localized } from '@lit/localize';
import { msg, str, initializeLocaleFromHA } from '../localization/localize.js';

// Home Assistant types
interface Hass {
  language?: string;
  states: Record<string, HassEntity>;
  callService: (domain: string, service: string, data?: Record<string, unknown>) => Promise<void>;
}

interface HassEntity {
  entity_id: string;
  state: string;
  attributes: Record<string, unknown>;
}

interface CardConfig {
  entity?: string;
  name?: string;
  show_state?: boolean;
}

@customElement('autosnooze-card')
@localized()
export class AutosnoozeCard extends LitElement {
  @property({ attribute: false }) hass?: Hass;
  @property({ attribute: false }) config?: CardConfig;

  @state() private _snoozeMinutes = 30;
  @state() private _isLoading = false;

  static styles = css`
    :host {
      display: block;
      padding: 16px;
    }

    .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 16px;
    }

    .card-title {
      font-size: 1.2em;
      font-weight: 500;
    }

    .status-badge {
      padding: 4px 8px;
      border-radius: 4px;
      font-size: 0.85em;
    }

    .status-active {
      background-color: var(--success-color, #4caf50);
      color: white;
    }

    .status-inactive {
      background-color: var(--disabled-color, #9e9e9e);
      color: white;
    }

    .quick-actions {
      display: flex;
      gap: 8px;
      flex-wrap: wrap;
      margin-bottom: 16px;
    }

    .quick-action-btn {
      padding: 8px 16px;
      border: 1px solid var(--primary-color, #03a9f4);
      border-radius: 4px;
      background: transparent;
      color: var(--primary-color, #03a9f4);
      cursor: pointer;
      transition: background-color 0.2s;
    }

    .quick-action-btn:hover {
      background-color: var(--primary-color, #03a9f4);
      color: white;
    }

    .quick-action-btn:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    .custom-duration {
      display: flex;
      gap: 8px;
      align-items: center;
      margin-bottom: 16px;
    }

    .custom-duration input {
      width: 80px;
      padding: 8px;
      border: 1px solid var(--divider-color, #e0e0e0);
      border-radius: 4px;
    }

    .action-buttons {
      display: flex;
      gap: 8px;
    }

    .primary-btn {
      padding: 10px 20px;
      background-color: var(--primary-color, #03a9f4);
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
    }

    .primary-btn:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    .secondary-btn {
      padding: 10px 20px;
      background-color: transparent;
      color: var(--primary-text-color, #212121);
      border: 1px solid var(--divider-color, #e0e0e0);
      border-radius: 4px;
      cursor: pointer;
    }

    .time-remaining {
      margin-top: 16px;
      padding: 12px;
      background-color: var(--card-background-color, #f5f5f5);
      border-radius: 4px;
      text-align: center;
    }

    .loading {
      opacity: 0.6;
      pointer-events: none;
    }
  `;

  protected updated(changedProperties: PropertyValues): void {
    super.updated(changedProperties);

    // Initialize locale when hass is first set or language changes
    if (changedProperties.has('hass') && this.hass) {
      const oldHass = changedProperties.get('hass') as Hass | undefined;
      if (!oldHass || oldHass.language !== this.hass.language) {
        initializeLocaleFromHA(this.hass);
      }
    }
  }

  setConfig(config: CardConfig): void {
    if (!config.entity) {
      throw new Error(msg('Entity is required', { id: 'validation.required' }));
    }
    this.config = config;
  }

  private get _entity(): HassEntity | undefined {
    if (!this.hass || !this.config?.entity) return undefined;
    return this.hass.states[this.config.entity];
  }

  private get _isSnoozeActive(): boolean {
    return this._entity?.state === 'on';
  }

  private get _cardTitle(): string {
    return this.config?.name || msg('Autosnooze', { id: 'card.title' });
  }

  private async _handleSnooze(minutes: number): Promise<void> {
    if (!this.hass || !this.config?.entity) return;

    this._isLoading = true;
    try {
      await this.hass.callService('autosnooze', 'snooze', {
        entity_id: this.config.entity,
        duration: minutes,
      });
    } catch {
      console.error('Failed to start snooze');
    } finally {
      this._isLoading = false;
    }
  }

  private async _handleStopSnooze(): Promise<void> {
    if (!this.hass || !this.config?.entity) return;

    this._isLoading = true;
    try {
      await this.hass.callService('autosnooze', 'stop', {
        entity_id: this.config.entity,
      });
    } catch {
      console.error('Failed to stop snooze');
    } finally {
      this._isLoading = false;
    }
  }

  private _handleCustomMinutesChange(e: Event): void {
    const input = e.target as HTMLInputElement;
    this._snoozeMinutes = parseInt(input.value, 10) || 30;
  }

  render() {
    if (!this._entity) {
      return html`
        <ha-card>
          <div class="card-content">
            ${msg('Entity not found', { id: 'error.not_found' })}
          </div>
        </ha-card>
      `;
    }

    return html`
      <ha-card class="${this._isLoading ? 'loading' : ''}">
        <div class="card-content">
          <div class="card-header">
            <span class="card-title">${this._cardTitle}</span>
            <span
              class="status-badge ${this._isSnoozeActive ? 'status-active' : 'status-inactive'}"
            >
              ${this._isSnoozeActive
                ? msg('Active', { id: 'snooze.status.active' })
                : msg('Inactive', { id: 'snooze.status.inactive' })}
            </span>
          </div>

          <div class="quick-actions">
            <button
              class="quick-action-btn"
              @click="${() => this._handleSnooze(15)}"
              ?disabled="${this._isLoading}"
              aria-label="${msg('Snooze for 15 minutes', { id: 'a11y.snooze_button' })}"
            >
              ${msg('15 min', { id: 'quick_snooze.15m' })}
            </button>
            <button
              class="quick-action-btn"
              @click="${() => this._handleSnooze(30)}"
              ?disabled="${this._isLoading}"
            >
              ${msg('30 min', { id: 'quick_snooze.30m' })}
            </button>
            <button
              class="quick-action-btn"
              @click="${() => this._handleSnooze(60)}"
              ?disabled="${this._isLoading}"
            >
              ${msg('1 hour', { id: 'quick_snooze.1h' })}
            </button>
            <button
              class="quick-action-btn"
              @click="${() => this._handleSnooze(120)}"
              ?disabled="${this._isLoading}"
            >
              ${msg('2 hours', { id: 'quick_snooze.2h' })}
            </button>
          </div>

          <div class="custom-duration">
            <input
              type="number"
              min="1"
              max="1440"
              .value="${String(this._snoozeMinutes)}"
              @change="${this._handleCustomMinutesChange}"
              aria-label="${msg('Custom duration in minutes', { id: 'a11y.time_picker' })}"
            />
            <span>${msg('minutes', { id: 'snooze.duration.minutes' })}</span>
            <button
              class="quick-action-btn"
              @click="${() => this._handleSnooze(this._snoozeMinutes)}"
              ?disabled="${this._isLoading}"
            >
              ${msg('Custom', { id: 'snooze.duration.custom' })}
            </button>
          </div>

          <div class="action-buttons">
            ${this._isSnoozeActive
              ? html`
                  <button
                    class="secondary-btn"
                    @click="${this._handleStopSnooze}"
                    ?disabled="${this._isLoading}"
                  >
                    ${msg('Stop Snooze', { id: 'snooze.action.stop' })}
                  </button>
                  <button
                    class="primary-btn"
                    @click="${() => this._handleSnooze(this._snoozeMinutes)}"
                    ?disabled="${this._isLoading}"
                  >
                    ${msg('Extend', { id: 'snooze.action.extend' })}
                  </button>
                `
              : html`
                  <button
                    class="primary-btn"
                    @click="${() => this._handleSnooze(this._snoozeMinutes)}"
                    ?disabled="${this._isLoading}"
                  >
                    ${msg('Start Snooze', { id: 'snooze.action.start' })}
                  </button>
                `}
          </div>

          ${this._isSnoozeActive && this._entity.attributes['remaining_time']
            ? html`
                <div class="time-remaining">
                  ${msg(str`Time remaining: ${this._entity.attributes['remaining_time']}`, {
                    id: 'snooze.time_remaining',
                  })}
                </div>
              `
            : ''}
        </div>
      </ha-card>
    `;
  }
}

// Card configuration element
@customElement('autosnooze-card-editor')
@localized()
export class AutosnoozeCardEditor extends LitElement {
  @property({ attribute: false }) hass?: Hass;
  @property({ attribute: false }) config?: CardConfig;

  static styles = css`
    :host {
      display: block;
      padding: 16px;
    }

    .form-row {
      margin-bottom: 16px;
    }

    label {
      display: block;
      margin-bottom: 4px;
      font-weight: 500;
    }

    input,
    select {
      width: 100%;
      padding: 8px;
      border: 1px solid var(--divider-color, #e0e0e0);
      border-radius: 4px;
      box-sizing: border-box;
    }
  `;

  private _handleEntityChange(e: Event): void {
    const select = e.target as HTMLSelectElement;
    this._updateConfig({ entity: select.value });
  }

  private _handleNameChange(e: Event): void {
    const input = e.target as HTMLInputElement;
    this._updateConfig({ name: input.value });
  }

  private _updateConfig(changes: Partial<CardConfig>): void {
    const newConfig = { ...this.config, ...changes };
    const event = new CustomEvent('config-changed', {
      detail: { config: newConfig },
      bubbles: true,
      composed: true,
    });
    this.dispatchEvent(event);
  }

  render() {
    const entities = this.hass
      ? Object.keys(this.hass.states).filter((id) => id.startsWith('autosnooze.'))
      : [];

    return html`
      <div class="form-row">
        <label>${msg('Entity', { id: 'editor.entity_label' })}</label>
        <select @change="${this._handleEntityChange}">
          <option value="">${msg('Select an entity', { id: 'entity.select' })}</option>
          ${entities.map(
            (entity) => html`
              <option value="${entity}" ?selected="${entity === this.config?.entity}">
                ${entity}
              </option>
            `
          )}
        </select>
      </div>

      <div class="form-row">
        <label>${msg('Name (optional)', { id: 'editor.name_label' })}</label>
        <input
          type="text"
          .value="${this.config?.name || ''}"
          @input="${this._handleNameChange}"
          placeholder="${msg('Autosnooze', { id: 'card.title' })}"
        />
      </div>
    `;
  }
}

// Register the card with Home Assistant
declare global {
  interface Window {
    customCards?: Array<{
      type: string;
      name: string;
      description: string;
    }>;
  }
}

window.customCards = window.customCards || [];
window.customCards.push({
  type: 'autosnooze-card',
  name: 'Autosnooze Card',
  description: 'A card to control autosnooze entities',
});
